<template>
    <div class="vbestui-radio-group" @change="change" :class="[vertical?'vbestui-radio-group-vertical':'']">
        <slot></slot>
    </div>
</template>
<script>
export default {
    name:'BRadioGroup',
    props:{
        value:null,
        border:{
            type:Boolean,
            default:false
        },
        button:{
            type:Boolean,
            default:false
        },
        vertical:{
            type:Boolean,
            default:false
        }
    },
    provide(){
        return{
            'group':this
        }
    },
    methods:{
        change(){
            this.$emit('change')
        }
    }
}
</script>