<template>
  <div :class="tbodyClass">
      <slot></slot>
  </div>
</template>
<script>
const prefixCls="vbestui-table-body"
export default {
  name: "BTableBody",
  data(){
    return{
      prefixCls:prefixCls
    }
  },
  computed: {
    tbodyClass() {
      return[
        `${this.prefixCls}`
      ]
    },
  },
};
</script>